<template>
  <div id="app"></div>
</template>

<script>
export default {
  name: "App",
  data () {
    return {
      map: null,
      city: '武汉',
      rentalInfoList: [],
      pois: []
    }
  },
  mounted() {
    this.initMap()
  },
  methods: {
      initMap () {
      let map = new BMap.Map('app'); // 创建地图实例
      map.centerAndZoom(this.city, 12); // 初始化地图，设置中心点坐标和地图级别
      map.enableScrollWheelZoom();   //启用滚轮放大缩小，默认禁用
      map.enableContinuousZoom();    //启用地图惯性拖拽，默认禁用


      //关于状态码    
      //BMAP_STATUS_SUCCESS   检索成功。对应数值“0”。    
      //BMAP_STATUS_CITY_LIST 城市列表。对应数值“1”。    
      //BMAP_STATUS_UNKNOWN_LOCATION  位置结果未知。对应数值“2”。    
      //BMAP_STATUS_UNKNOWN_ROUTE 导航结果未知。对应数值“3”。    
      //BMAP_STATUS_INVALID_KEY   非法密钥。对应数值“4”。    
      //BMAP_STATUS_INVALID_REQUEST   非法请求。对应数值“5”。    
      //BMAP_STATUS_PERMISSION_DENIED 没有权限。对应数值“6”。(自 1.1 新增)    
      //BMAP_STATUS_SERVICE_UNAVAILABLE   服务不可用。对应数值“7”。(自 1.1 新增)    
      //BMAP_STATUS_TIMEOUT   超时。对应数值“8”。(自 1.1 新增)   

      // 获取当前地理位置
      let geolocation = new BMap.Geolocation();
      geolocation.getCurrentPosition(function (r) {
        if (this.getStatus() === BMAP_STATUS_SUCCESS) {
          let mk = new BMap.Marker(r.point);
          map.addOverlay(mk);
          // 移动地图中心点
          map.panTo(r.point);
          mk.setAnimation(BMAP_ANIMATION_BOUNCE); /*动画跳动*/
        } else {
          alert('get current position failed:' + this.getStatus())
        }
      }, { enableHighAccuracy: true });

      this.map = map;

      // 获取房屋信息
      this.getRentalInfo();
    },
    getRentalInfo () {
      this.$http({
        url: '/rental/getInfos',
        methods: 'get'
      }).then (res => {
        this.rentalInfoList = res.data.data
        this.display();
      })
      .catch(err => {
        console.log(err)
      })

    },
    // 搜索小区并绘制
    display () {
      this.rentalInfoList.forEach(house => {
      let map = this.map;
      let pois = this.pois;
      let localSearch = new BMap.LocalSearch(map);
      localSearch.setSearchCompleteCallback(function (house) {

          let hasShowedUrls = [];

          return searchResult => {
            const { url, desc, img, location } = house;

            // 避免重复
            if (hasShowedUrls.includes(url)) {
              return;
            }
            hasShowedUrls.push(url);
            // 获取地理位置信息
            let poi = searchResult.getPoi(0);
            if (!poi) {
              return;
            }
            // 查找是否有已存在的小区
            let index = pois.findIndex(value => {
                return  value.point.lng === poi.point.lng && value.point.lat === poi.point.lat
            })

            // 绘制小区地图点
            let point = new BMap.Point(poi.point.lng,poi.point.lat)
            let temp = null;
            // 已经存在
            if (index !== -1) {

              pois[index].urls.push(url);
              pois[index].descs.push(desc);
              pois[index].imgs.push(img);


              pois[index].marker.addEventListener('click', function (info) {
                return function () {
                  let opts = {
              		  	width : 200,    
              		  	height: 200 * info.urls.length,     
              		  	title : poi.title, 
              		  	enableMessage: true,
                  }

                  let message = ''
                  info.urls.forEach((url, index) => {
                    message += `<div><a href=${url}url><img title='点击访问' class='img-responsive showImg' alt='Responsive image' src=${info.imgs[index]}></img></a></div>`
                  })
                 
                  map.openInfoWindow(new BMap.InfoWindow(message, opts), point)
                }
              }(pois[index]))
            } else {
              // 创建新的标注对象
              const House_Icon = new BMap.Icon('https://pic.liesio.com/2020/10/16/35731ca6a0dbb.png', new BMap.Size(30, 30), {});
    
              poi.marker =  new BMap.Marker(point, {icon: House_Icon});;
              map.addOverlay(poi.marker);
              poi.urls = [url];
              poi.imgs = [img];
              poi.descs = [desc];


             poi.marker.addEventListener('click', function () {

                  let opts = {
              		  	width : 200,   
              		  	title : poi.title, 
              		  	enableMessage: true,
                  }
                  let message = "<div><p class='desc'>"+ poi.descs[0] +"</p><a href="+url+"><img title='点击访问' class='img-responsive showImg' alt='Responsive image' src="+poi.imgs[0]+"></img></a></div>"
                  map.openInfoWindow(new BMap.InfoWindow(message, opts), point)
              })
              pois.push(poi)
            }
          }
        }(house));
      localSearch.search(this.city + house.location)
      })
    }
  },
};
</script>

<style>
#app {
  height: 100%;
  width: 100%;
}
div {
  text-align: center;
}
p {
  text-align: left;
}
.showImg{
	width: 200px;
	height: 160px;
	margin: 10px auto;
	cursor: pointer;
}
.desc {
  font-size: 12px;
}
</style>
